body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.title-app {
    width: 200px;
    display: -webkit-flex;
    display: flex;
    margin: auto;
    padding-top: 20px;
    padding-bottom: 20px;
}
.poke-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-family: 'Comfortaa', cursive;
  list-style: none;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 20px; }

.pokemon {
  width: 120px;
  height: 150px;
  border: 2px solid black;
  border-radius: 15px;
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 15px;
  overflow: hidden; }

.poke-name {
  font-size: 14px;
  margin-top: 8px; }

.poke-img {
  width: 65px;
  height: 65px; }

.container-id {
  font-size: 8px;
  font-weight: 700;
  display: -webkit-flex;
  display: flex;
  width: 120px;
  position: absolute;
  z-index: 1;
  margin-top: 55px; }

.poke-id {
  background-color: white;
  border: 2px solid black;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: auto; }

.container-type {
  background-color: red;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 60px;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 5px;
  border-top: 2px solid black; }

.poke-type {
  font-weight: 700;
  font-size: 10px;
  height: 20px;
  border: 1px solid white;
  border-radius: 5px;
  padding-top: 3px;
  padding-left: 4px;
  padding-right: 4px; }

.container-input {
  font-family: 'Comfortaa', cursive;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 12; }

.input-title {
  font-weight: 700;
  margin-bottom: 10px; }

.input-name {
  text-align: center;
  border: 1px solid #333232;
  box-shadow: 2px 2px 10px #666;
  width: 200px;
  height: 25px; }

